import App from './App.vue';
import './global.css';
// 
async function waitForDependencies(maxWait = 15000) {
    const checkInterval = 100;
    const startTime = Date.now();
    // 
    const getMissingDeps = () => {
        const missing = [];
        if (typeof getCurrentMessageId !== 'function')
            missing.push('getCurrentMessageId');
        if (typeof getChatMessages !== 'function')
            missing.push('getChatMessages');
        if (typeof Mvu === 'undefined')
            missing.push('Mvu');
        if (typeof eventOn !== 'function')
            missing.push('eventOn');
        return missing;
    };
    const checkDeps = () => getMissingDeps().length === 0;
    // 
    if (checkDeps())
        return;
    // 
    while (Date.now() - startTime < maxWait) {
        await new Promise(resolve => setTimeout(resolve, checkInterval));
        if (checkDeps())
            return;
    }
    const missing = getMissingDeps();
    console.warn('[] ', {
        missing,
        href: window.location?.href,
        referrer: document.referrer,
        hasFrameElement: !!window.frameElement,
    });
}
$(async () => {
    // 
    await waitForDependencies();
    //  MVU 
    if (typeof waitGlobalInitialized === 'function') {
        await waitGlobalInitialized('Mvu');
    }
    const scrollToTopOnce = (() => {
        let done = false;
        return () => {
            if (done)
                return;
            done = true;
            const run = () => {
                try {
                    document.documentElement.scrollTop = 0;
                    document.body.scrollTop = 0;
                    window.scrollTo({ top: 0, left: 0, behavior: 'auto' });
                }
                catch {
                    // ignore
                }
            };
            // /iframe
            run();
            requestAnimationFrame(run);
            setTimeout(run, 150);
        };
    })();
    //  Vue 
    const app = createApp(App);
    app.use(createPinia());
    app.mount('#app');
    //  iframe 
    scrollToTopOnce();
    // BFCache/
    window.addEventListener('pageshow', () => {
        scrollToTopOnce();
    }, { once: true });
});
// 
$(window).on('pagehide', () => {
    eventClearAll();
});
